import React from 'react'
import Link from 'gatsby-link'

const SecondPage = () => {
  return (
      <div>
        <h1>Front End Development</h1>
        <p>This section derives of majority anyone who's really spreading the knowledge of your front-end section of a website. So expect a lot of HTML, CSS, Javascript like tutorials.</p>
        <Link to="/">Go back to the homepage</Link>
        <People />
      </div>
  );
}

const People = () => {
    return (
      <div>
        <Person />
      </div>
    );
}

const Person = () => {
  return (
    <div>
      <img src="#"/>
      <p>Sub Count: 9000</p>
      <h2>Yeet</h2>
      <p>Recently growing channel with tons of potential on teaching users how to create their own applications.</p>
    </div>
  );
}

export default SecondPage



